import React from "react";
import PropTypes from "prop-types";
import Typography from "@material-ui/core/Typography";

export default function Title(props: any) {
  return (
    <Typography
      component="h2"
      variant="h6"
      color="primary"
      gutterBottom
      style={props.style}
    >
      {props.children}
    </Typography>
  );
}

Title.propTypes = {
  children: PropTypes.node,
  style: PropTypes.object,
};
